import React from 'react';
import { Link } from 'react-scroll';
import { Collapse, Navbar, NavbarToggler, NavbarBrand, Nav, NavItem, NavLink } from 'reactstrap';

export default class ProfileNavbar extends React.Component
{
  constructor(props) 
  {
    super(props);

    this.toggleNavbar = this.toggleNavbar.bind(this);
    this.state = {
      collapsed: true
    };
  }

  toggleNavbar() 
  {
    this.setState({
      collapsed: !this.state.collapsed
    });
  }

  render()
  {
    const { name, image, sections, focusedSection } = this.props;

    const navItems = sections.map((section, index) =>
    {
      let activeClass = '';
      if (section.id === focusedSection)
      {
        activeClass = 'active';
      }

      return (
        <NavItem key={index}>
          <Link to={section.id}
            spy={true}
            smooth={true}
            hashSpy={true}
            offset={50}
            duration={500}
            delay={1000}
            isDynamic={true}
            ignoreCancelEvents={false}>
            <NavLink className={activeClass}>{section.name}</NavLink>
          </Link>
        </NavItem>
      );
    });

    return (
      <div>
        <Navbar expand="lg" fixed="top" dark className="bg-primary">
          <NavbarBrand href="/" className="mr-auto">
            <span>
              <img className="img-fluid img-profile rounded-circle mx-auto mb-2" src={image} alt="" />
            </span>
            <span className="navbar-name">{name}</span>
          </NavbarBrand>
          <NavbarToggler onClick={this.toggleNavbar} className="mr-2" />
          <Collapse isOpen={!this.state.collapsed} navbar>
            <Nav navbar>
              {navItems}
            </Nav>
          </Collapse>
        </Navbar>
      </div>
    );
  }
};